import React, { Component } from "react";
import { NavbarBrand, Container } from "react-bootstrap";
import ProfilePic from "./pic.jpg";
import "./Navbar.css";
class Navbar extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div>
        <nav className="navbar navbar-dark bg-dark">
          <Container>
            <NavbarBrand href="/">
              <img src={ProfilePic} className="profile-pic" alt=" " />
              <span className="name">Sai Hari Chandan Morapakala</span>
            </NavbarBrand>
          </Container>
        </nav>
      </div>
    );
  }
}

export default Navbar;
