import React, { Component } from "react";
import "./About.css";
import { Container } from "react-bootstrap";
class About extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <Container fluid={true}>
        <p className="body">
          I'm currently working as a Data Scientist at Caterpillar Inc, India.
          My preliminary intrests span the fields of Compter Vision and Deep
          Learning. Previously, I have completed my bachelors in Computer
          Science and Engineering from Amrita Vishwa Vidyapeetham, Amritapuri
          Campus. Apart from model building, I am also interested in deploying
          them on cloud platforms. In my free time I try to solve problems on
          Kaggle or read about the evolving technologies.
        </p>
      </Container>
    );
  }
}

export default About;
